@import url('https://fonts.googleapis.com/css2?family=Lora:wght@400;600;700&family=Oswald:wght@400;500;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary: #f04f3c;
  --secondary: #9c271a;
  --neutral: #c5c6c7;
  --neutral-dark: #6b6b6b;
  --background-main: #fff;
  --background-fore: #f5f6ff;
  --text: #172038;
}

html {
  scroll-behavior: smooth;
}

body {
  background: var(--background-main);
  height: 100vh;
}

h1 {
  color: var(--primary);
  font-family: 'Oswald', sans-serif;
}

h3 {
  color: var(--text);
  font-family: 'Oswald', sans-serif;
}

p {
  color: var(--text);
  font-family: 'Lora', sans-serif;
}

.App {
  display: flex;
}

@media (orientation: portrait) {
  .App {
    flex-direction: column;
  }

  div#sidebar, div#raffle, div#home {
    width: 100%;
    height: 100%;
  }

  div#main {
    border-top: 1px solid var(--neutral);
  }
}